// =====================================
// Breakpoints
// =====================================

$breakpoints: (
    tiny: "only screen and (min-width: 400px)",
    smallest: "only screen and (min-width: 550px)",
	mobile: "only screen and (min-width: 650px)",
	small-tablet: "only screen and (min-width: 750px)",
	tablet: "only screen and (min-width: 800px)",
	tablet-landscape: "only screen and (min-width: 1024px)",
	desktop: "only screen and (min-width: 1366px)",
	cinema: "only screen and (min-width: 1600px)",
	table: "only screen and (max-width: 799px)",
	hidpi: "(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)",
	laptop-short: "only screen and (max-height: 900px) and (min-width: 700px)"
);

// =====================================
// Fonts
// =====================================

$font1: cormorant-garamond, serif;
$font2: 'Montserrat', sans-serif;
